import React, { useEffect, useState } from "react";
import {
  Heading,
  Accordion,
  AccordionPanel,
  Box,
  Button,
  ResponsiveContext,
  Tabs,
  Tab
} from "grommet";
import Chatroom from "./Chatroom";
import ReactHtmlParser from "react-html-parser";
import LiveStreamCommerce from "../containers/LiveStreamCommerce";
import ProductListing from "../containers/ProductListing";
import Parse, {
  hasRole,
  getMyRoles,
  searchFirstStore,
  searchProduct,
} from "../library/parse";
import { Chat } from "grommet-icons";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import { faWhatsapp } from "@fortawesome/free-brands-svg-icons";
import { faComment } from "@fortawesome/free-solid-svg-icons";

import Order from "./Order";

const getUserRole = (roles) => roles.find((e) => e.includes("User"));

const Home = ({ location, pathContext, pageContext, id, ...props }) => {
  const size = React.useContext(ResponsiveContext);
  const [enableLiveStream, setEnableLiveStream] = useState(false);
  const [roles, setRoles] = useState([]);
  const [store, setStore] = useState(null);
  const [currentUser, setCurrentUser] = useState(Parse.User.current());

  const [activeAccordionPanel, setActiveAccordionPanel] = useState(1);
  const storeName = store && store.get("name");
  const storeInformation =
    store && ReactHtmlParser(`<div>${store.get("description")}`);
  const isStoreOwner = roles.includes(`Store-${id}`);
  useEffect(() => {
    currentUser && getMyRoles().then((e) => setRoles(e));
  }, [id]);

  useEffect(() => {
    searchFirstStore({
      objectId: {
        f: "equalTo",
        v: id,
      },
    }).then((e) => {
      setStore(e);
      setEnableLiveStream(e && e.get("allowLive"));
    });
  }, [id]);
  return (
    <>
      <Heading level={3}> {storeName} </Heading>
      <Accordion
        onActive={setActiveAccordionPanel}
        activeIndex={activeAccordionPanel}
      >
        <AccordionPanel label="More Information">
          <Box pad="medium" background="light-2">
            <Tabs>
              <Tab title="Information">

                {storeInformation}
              </Tab>
              <Tab title="My Active Bids">
                <Order storeId={id}/>
              </Tab>
              <Tab title="Contact" gap="small">
                <Box margin="xsmall" direction="row" gap="small">
                
                {
                  store && store.get('whatsapp') && <Button
                  primary
                  icon={<FontAwesomeIcon
                    icon={faWhatsapp}
                  />}
                  href={`https://wa.me/${store.get('whatsapp')}`}
                  label="Whatsapp"
                  />
                } 
                <Button
                  primary
                  icon={<FontAwesomeIcon
                    icon={faComment}
                  />}
                  href=''
                  label="Direct Message"
                  href={`/chat/r/${getUserRole(roles)}/c/Store-${id}`}
                />
                </Box>
              </Tab>
            </Tabs>
          </Box>
        </AccordionPanel>
      </Accordion>
      <br />
      <Box gap="small" direction={size === "small" ? "column" : "row"}>
        <Box>
          {enableLiveStream && (
            <Box width={size === "small" ? "100%" : "720px"} height="405px">
              <LiveStreamCommerce />
            </Box>
          )}
        </Box>
        {
          <Box height="405px" width={size === "small" ? "column" : "100%"}>
            <Chatroom
              senderRoleName={
                currentUser
                  ? isStoreOwner
                    ? `Store-${id}`
                    : `User-${currentUser.id}`
                  : false
              }
              recipientRoleName={`Store-${id}`}
              publicRoom={true}
              placeholder="talk to everyone in the room"
            />
          </Box>
        }
      </Box>
      <ProductListing storeId={id} editMode={isStoreOwner} />
    </>
  );
};

export default Home;
