import React, { useEffect, useState } from "react";
import { Box, List } from "grommet";
import { getCartForProduct, deleteCart } from "../../library/parse";
import { Close } from "grommet-icons";

const Component = ({ product }) => {
  const [listData, setListData] = useState([]);
  const loadCart = () => {
    getCartForProduct(product).then((list) =>
      setListData(list.map((e) => e.toJSON()))
    );
  };
  useEffect(() => {
    loadCart();
  }, []);
  return (
    <Box height={{ max: "80px" }}>
      <List
        margin="small"
        primaryKey="name"
        secondaryKey="percent"
        data={listData}
        children={(datum) => {
          return (
            <Box direction="row">
              <Box>{datum.bid}</Box>
              <Box margin={{ left: "auto" }}>
                <Close
                  onClick={() => {
                    deleteCart(datum.objectId).then(() => loadCart());
                  }}
                />
              </Box>
            </Box>
          );
        }}
      />
    </Box>
  );
};
export default Component;
